<template>
    <div style="margin:30px">
        <el-divider></el-divider>
        <el-descriptions class="margin-top" title="实验组信息" :column="3" :size="size">
            <template slot="extra">
                 <router-link :to="{ name: '活动管理', params: {campaign_id: this.$route.params.campaign_id}}" style='text-decoration: none;color: aliceblue;' align="right">
                    <el-button class="" ><i class="el-icon-arrow-left" style="margin-right: 10px;"></i>返回活动管理</el-button>
                </router-link>
            </template>
            <el-descriptions-item label="活动名称">{{abMetaData.camp_name}}</el-descriptions-item>
            <el-descriptions-item label="实验名称">{{abMetaData.abname}}</el-descriptions-item>
            <el-descriptions-item label="创建于">
                <timeago :datetime="abMetaData.creation_date" locale="zh-CN"></timeago>
            </el-descriptions-item>
            <!--<el-descriptions-item label="实验类型">
                <el-tag size="small" type="primary">{{abMetaData.app_type}}</el-tag>
            </el-descriptions-item>-->
            <el-descriptions-item label="实验描述">{{abMetaData.description}}</el-descriptions-item>
        </el-descriptions>
        <el-collapse v-model="activeNames">
          <el-collapse-item title="实验实例信息" name="1">
              <el-col :span="24" class="toolbar" style="padding-bottom: 10px;">
                  <router-link :to="{ name: '实验详情',params: {ab_id: this.$route.params.ab_id}}" style='text-decoration: none;color: #000000;'>
                      <el-button type="primary" style="margin-left:15px">
                          <i class="el-icon-search" style="margin-right: 5px;margin-left:10px"></i>查看详情</el-button>
                  </router-link>
              </el-col>
              <el-table :data="altListData" highlight-current-row style="width: 100%;">
                        <el-table-column prop="alt_name" label="实验组名称" min-width="30%">
                        </el-table-column>
                         <el-table-column prop="description" label="实验组描述" min-width="30%">
                        </el-table-column>
                        <el-table-column prop="exposure_ratio" label="流量比例(%)" min-width="15%">
                        </el-table-column>
                         <el-table-column prop="model_name" label="使用策略" min-width="15%">
                        </el-table-column>
                         <el-table-column prop="creation_date" label="创建时间" min-width="15%">
                             <template slot-scope="scope">
                                <timeago :datetime="scope.row.creation_date" locale="zh-CN"></timeago>
                             </template>
                        </el-table-column>
                    </el-table>
          </el-collapse-item>
        </el-collapse>
    </div>
</template>

<script>

import { test } from '@/api/api'
import $ from 'jquery'
import moment from 'moment'

export default {
  data () {
    return {
        activeNames:["1"],
        abMetaData:{},
      firstID: 'all',
      altListData: [],
      moment: moment,
      project: '',
      case: '',
      ApiList: [],
      listLoading: false,
      searchName: '',
      total: 0,
      page: 1,
      sels: [], // 列表选中列
      activeIndex: ''
    }
  },
  methods: {
    expandExpClick (val) {
      this.getExpAltList(val.id)
      // console.log("expandExpClick",this.altListData,val.id)
    },
    getAbMetaData () {
      this.listLoading = true
      let self = this
      console.log(self.page, this.$route.params.ab_id, 'datadatadata')
      $.ajax({
        type: 'get',
        url: test + '/api/campaigns/get_abexp_metadata',
        async: true,
        data: { ab_id: this.$route.params.ab_id},
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          self.listLoading = false
          if (data.code === '999999') {
            self.abMetaData = data.data.data
            // self.ApiList = data.data.data
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    // 获取实验候选
    getExpAltList (expId) {
      let self = this
      $.ajax({
        type: 'get',
        url: test + '/api/campaigns/ab_exp_altlist',
        async: true,
        data: { ab_id: expId,//this.$route.params.ab_id,
               campaign_id: this.$route.params.campaign_id },
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          if (data.code === '999999') {
            self.altListData = data.data.data
              console.log(self.altListData,"altListData")
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    handleCurrentChange (val) {
      this.page = val
      this.getAbMetaData()
    }

  },
  mounted () {
    this.getAbMetaData()
    this.getExpAltList(this.$route.params.ab_id)
    this.case = this.$route.params.ab_id
  }
}
</script>

